import React, { useState } from "react";
import axios from "axios";
import NavBar from "./compenents/NavBar";
import Footer from "./compenents/Footer";
import Loding from "./compenents/Loding";

function App() {
  const [url, setUrl] = useState("");
  const [isActive, setActive] = useState(false);
  const [loding, setLoding] = useState(false);
  const [host, setHost] = useState('https://nodejs-react-youtube-downloade.herokuapp.com');
  const [info, setInfo] = useState({});

  const getInfoDownload = async () => {
    if (url.trim() === "") {
      alert("please enter valid url");
      return;
    }
    setLoding(true);
    try {
      const data = await axios.get(`${host}/info/download`, {
        params: { url: url },
      });

      setInfo(data);
      setActive(true);
      setLoding(false);
    } catch (error) {
      alert("pleas try agin ! :)");
    }
  };
  const downloadThis = (data) => {
    window.open(
      `${host}/downloads?url=${url}&itag=${data.itag}&container=${data.container}`
    );
  };

  return (
    <div className="container">
      <NavBar />
      <div className="download">
        <div className="center">
          <input
            type="text"
            className="input"
            placeholder="link here"
            onChange={(e) => setUrl(e.target.value)}
          />
          <button className="btn" onClick={(e) => getInfoDownload(e)}>
            download{" "}
          </button>
        </div>
      </div>

      <div className="download-info">
       {loding && (<div className="info"><Loding /></div>) }
      
        {isActive ? (
          <>
            <div className="info">
             
              <img
                src={info.data.videoDetails.thumbnails[0].url}
                alt="thumbnails"
                className="thub"
              />
              <h5 className="di">{info.data.videoDetails.title}</h5>
            </div>
            <div className="downloadBtn">
              <>
                {info.data.formats.map((data, i) => (
                  <button
                    className="downBtn"
                    value={data}
                    onClick={(e) => downloadThis(data)}
                    key={i}
                  >
                    {data.container} {data.qualityLabel}
                  </button>
                ))}
              </>
            </div>
          </>
        ) : (
          ""
        )}
      </div>

      <Footer />
    </div>
  );
}

export default App;

